$prefix: '.rise-blocks';

/* Grid */
#{$prefix}-icon-list {
    display: flex;
    flex-wrap: wrap;
}

#{$prefix}-icon-list {
  #{$prefix}-content {
    #{$prefix}-title {
      margin: 0 0 0 10px;
    }
  }
}

.wp-block-rise-blocks-icon-lists {
    .rise-blocks-icon-list {
        align-items: center;
    }
    
    &.aligncenter {
        .rise-blocks-icon-list{
            justify-content: center;
        }
    }
    &.alignright {
        .rise-blocks-icon-list{
            justify-content: right;
        }
    }
    .rise-blocks-block-lists{
        >div {
            position: relative;
            flex: 100%;
            p {
                margin: 0;
            }
        
            .rise-blocks-icon {
                margin-right: 5px;
            }
            
        }
    } 
}

.rise-blocks-block-lists>div:last-child:after {
    display: none;
}